/**
 * 
 *
 * 
 */
import { create } from "zustand";
import { API_BASE_URL } from "../lib/config";

/**  */
export interface SearchResult {
  sessionId: string;
  sessionName: string;
  shortSummary: string;
  relevanceScore: number;
}

/**  */
export interface SearchStatus {
  available: boolean;
  indexedCount: number;
  message: string;
}

/**  */
interface SearchState {
  /**  */
  query: string;
  /**  */
  results: SearchResult[];
  /**  */
  totalResults: number;
  /**  */
  isSearching: boolean;
  /**  */
  error: string | null;
  /**  */
  status: SearchStatus | null;
  /**  */
  isLoadingStatus: boolean;
}

/**  */
interface SearchActions {
  /**  */
  setQuery: (query: string) => void;
  /**  */
  search: (query: string, limit?: number) => Promise<void>;
  /**  */
  clearResults: () => void;
  /**  */
  fetchStatus: () => Promise<void>;
}

/** API  URL */
const API_BASE = API_BASE_URL;

export const useSearchStore = create<SearchState & SearchActions>((set) => ({
  // 
  query: "",
  results: [],
  totalResults: 0,
  isSearching: false,
  error: null,
  status: null,
  isLoadingStatus: false,

  // 
  setQuery: (query: string) => set({ query }),

  // 
  search: async (query: string, limit = 10) => {
    if (!query.trim()) {
      set({ results: [], totalResults: 0, error: null });
      return;
    }

    set({ isSearching: true, error: null, query });

    try {
      const response = await fetch(`${API_BASE}/api/search`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query, limit }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Search failed");
      }

      const data = await response.json();
      set({
        results: data.results,
        totalResults: data.totalResults,
        isSearching: false,
      });
    } catch (error) {
      set({
        error: error instanceof Error ? error.message : "Search failed",
        isSearching: false,
        results: [],
        totalResults: 0,
      });
    }
  },

  // 
  clearResults: () =>
    set({
      query: "",
      results: [],
      totalResults: 0,
      error: null,
    }),

  // 
  fetchStatus: async () => {
    set({ isLoadingStatus: true });

    try {
      const response = await fetch(`${API_BASE}/api/search/status`);

      if (!response.ok) {
        throw new Error("Failed to fetch search status");
      }

      const data = await response.json();
      set({ status: data, isLoadingStatus: false });
    } catch (error) {
      set({
        status: {
          available: false,
          indexedCount: 0,
          message:
            error instanceof Error
              ? error.message
              : "Failed to fetch search status",
        },
        isLoadingStatus: false,
      });
    }
  },
}));
